.header--header {
  position: fixed;
  width: 100%;
  /* padding: 22px 26px; */

  background-color: var(--white);
  background-image: url('../../img/bg-wave-fade.svg');
  background-repeat: no-repeat;

  z-index: 2;
}

.header--header-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 20px 26px;
}

.header--header-text {
  font-size: 12px;
  font-weight: 300;
}

.header--header-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}



