.button--wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  background-color: var(--white);
  color: var(--black);

  height: 60px;
  width: 60px;
  border: 1px solid rgb(194, 194, 194);
  border-radius: 50%;

  font-size: 30px;

  margin: 0;
  padding: 0;
}

.button--wrap:hover {
  background-color: var(--white);
  color: var(--black);
  outline: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
}

.button--wrap:focus {
  background-color: var(--white);
  outline: none;
  color: var(--black);
}

.button--wrap:active {
  background-color: var(--white);
  color: var(--black);
}

.button--wrap:hover,
.button--wrap:active,
.button--wrap:focus:active {
  background-color: var(--white);
  color: var(--black);
  -webkit-box-shadow: none;
          box-shadow: none;
  outline: none;
  background-image: none;
}



